<template>
	<Content :class="{'icon-loading': loading}" app-name="mopidy">
		<AppNavigation>
			<template id="mopidy-navigation" #list>
				<div style="margin-bottom: 5em">
					<AppNavigationItem title="Soundcloud Streams"
						icon="icon-folder"
						:allow-collapse="true">
						<template>
							<AppNavigationItem v-if="!librarySoundcloud" :loading="!librarySoundcloud" title="Loading..." />
							<AppNavigationItem v-for="lib in librarySoundcloud"
								:key="lib.name"
								:title="lib.name"
								@click="pickTrack(lib)" />
						</template>
					</AppNavigationItem>
					<AppNavigationItem title="SomaFM Channels"
						icon="icon-folder"
						:allow-collapse="true">
						<template>
							<AppNavigationItem v-if="!librarySomafm" :loading="!librarySomafm" title="Loading..." />
							<AppNavigationItem v-for="lib in librarySomafm"
								:key="lib.name"
								:title="lib.name"
								@click="pickTrack(lib)" />
						</template>
					</AppNavigationItem>
				</div>
			</template>
		</AppNavigation>
		<AppContent>
			<div v-if="trackDetail">
				<h2 v-text="trackDetail.name" />
				<img v-if="trackDetail && trackDetail.album" :src="trackDetail.album.images[0] ||''">
				<div v-if="trackDetail">
					<span>Genre:</span>
					<strong v-text="trackDetail.genre" />
				</div>
			</div>
			<div>
				<button :disabled="playerState === 'playing'" @click="resumePlayback()">Resume</button>
				<button :disabled="playerState === 'paused'" @click="pausePlayback()">Pause</button>
			</div>
			<div>
				<div class="slidecontainer">
					<input type="range"
						min="1"
						max="100"
						:value="playerVolume">
				</div>
			</div>
		</AppContent>
	</Content>
</template>

<script>
import Content from '@nextcloud/vue/dist/Components/Content'
import AppContent from '@nextcloud/vue/dist/Components/AppContent'
import AppNavigation from '@nextcloud/vue/dist/Components/AppNavigation'
import AppNavigationItem from '@nextcloud/vue/dist/Components/AppNavigationItem'
// import AppNavigationNew from '@nextcloud/vue/dist/Components/AppNavigationNew'
// import AppNavigationSettings from '@nextcloud/vue/dist/Components/AppNavigationSettings'
// import AppNavigationCounter from '@nextcloud/vue/dist/Components/AppNavigationCounter'
// import ActionButton from '@nextcloud/vue/dist/Components/ActionButton'
// import ActionLink from '@nextcloud/vue/dist/Components/ActionLink'
// import AppNavigationIconBullet from '@nextcloud/vue/dist/Components/AppNavigationIconBullet'
// import ActionCheckbox from '@nextcloud/vue/dist/Components/ActionCheckbox'
// import ActionInput from '@nextcloud/vue/dist/Components/ActionInput'
// import ActionRouter from '@nextcloud/vue/dist/Components/ActionRouter'
// import ActionText from '@nextcloud/vue/dist/Components/ActionText'
// import ActionTextEditable from '@nextcloud/vue/dist/Components/ActionTextEditable'

import Mopidy from 'mopidy'

const mopidy = new Mopidy({ webSocketUrl: 'wss://nextcloudpi/mopidy-ws/' })

export default {
	name: 'App',
	components: {
		Content,
		AppContent,
		AppNavigation,
		AppNavigationItem,
		// AppNavigationNew,
		// AppNavigationSettings,
		// AppNavigationCounter,
		// ActionButton,
		// ActionLink,
		// AppNavigationIconBullet,
		// ActionCheckbox,
		// ActionInput,
		// ActionRouter,
		// ActionText,
		// ActionTextEditable,
	},

	data() {
		return {
			loading: false,
			librarySoundcloud: undefined,
			librarySomafm: undefined,
			trackDetail: 'No info yet',
			playerState: '',
			playerVolume: 0,
		}
	},
	created() {

	},
	mounted() {
		mopidy.on('state:online', this.loadData.bind(this))
		mopidy.on('event:playbackStateChanged', this.updatePlaybackState.bind(this))
		mopidy.on('event:trackPlaybackStarted', this.onTrackStarted.bind(this))
		mopidy.on('event:streamTitleChanged', this.onStreamTitleChanged.bind(this))
		// mopidy.on('websocket:outgoingMessage', x => console.log('OUTGOING', x))
		// mopidy.on('websocket:incomingMessage', x => console.log('INCOME', x))

		setInterval(() => this.fetchTrackInfo(), 5000)
	},
	methods: {
		async loadData() {
			this.librarySoundcloud = await mopidy.library.browse({ uri: 'soundcloud:directory:stream' })
			console.log(this.librarySoundcloud)

			this.librarySomafm = await mopidy.library.browse({ uri: 'somafm:root' })
			console.log(this.librarySomafm)

			this.fetchTrackInfo()
			this.playerState = await mopidy.playback.getState()

		},
		updatePlaybackState(newState, timePosition) {
			console.log('#### updatePlaybackState', newState, timePosition)
			if (timePosition) {
				// this.info = `${newState} at ${timePosition / 1000}s`
			} else {
				// this.info = newState
			}
		},
		onTrackStarted(newState) {
			console.log('#### onTrackStarted ###', newState)
		},
		fetchTrackInfo() {
			mopidy.playback.getCurrentTrack().then(x => { this.trackDetail = x })
		},
		onStreamTitleChanged(title) {
			console.log(title)
			this.info = title
		},
		async pausePlayback() {
			await mopidy.playback.pause()
			this.playerState = await mopidy.playback.getState()
		},
		async resumePlayback() {
			await mopidy.playback.resume()
			this.playerState = await mopidy.playback.getState()
		},
		async pickTrack(item) {
			await mopidy.tracklist.clear()
			const pickedTracks = await mopidy.tracklist.add({ uris: [item.uri] })
			console.log('### PCIKED TRACK ###', pickedTracks)

			await mopidy.playback.play({ tlid: 1 })
			// this.fetchTrackInfo()
		},
		addOption(val) {
			this.options.push(val)
			this.select.push(val)
		},
		previous(data) {
			console.debug(data)
		},
		next(data) {
			console.debug(data)
		},
		close(data) {
			console.debug(data)
		},
		newButtonAction(e) {
			console.debug(e)
		},
		log(e) {
			console.debug(e)
		},

	},
}
</script>
